.header {
    width: 100%;
    position: fixed;
    top: 100px;
    background-Color: var(--clr-funct-bg);
    box-Shadow: 0 4px 6px var(--clr-funct-bg);
    color: var(--clr-funct-high-light-1);
    z-index: 500;
    height: 50px;
    text-align: center;
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}


.navlink{
    color: #ffffff;
}

@media (max-width: 500px) {
    .header {
        height: 100px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;
        top: 150px;
    }
}

@media (max-width: 404px) {
    .header {
        top: 200px;
    }
}

